import React from "react";
import ButtonBase from '@material-ui/core/ButtonBase';
import { Button } from "@material-ui/core";

const ScheduleTimeRow = (props) =>{
    return(
        <ul class="box-scheduleTimeRow">
        
        <li class="box-scheduleHeader"></li>
        {props.timeList.map((value,index)=>{
            if(index%2==0){
            return(<li class="item-scheduleTimeRow box-even">{value}</li>)
        }else{
            return(<li class="item-scheduleTimeRow box-odd">{value}</li>)
        }
        })}
        </ul>
    )
}
export default ScheduleTimeRow;